  font-family: 'Raleway Medium'; }
  .main-menu .menu a:hover {
    text-decoration: none; }

.main-menu .menu > li > a {
  color: #d6d6d6;
  text-transform: uppercase;
  font-size: 1em;
  padding: 20px 10px;
  position: relative;
  overflow: hidden;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out; }
  @media only screen and (min-width: 980px) and (max-width: 1240px) {
    .main-menu .menu > li > a {
      font-size: 1.2vw; } }
  .main-menu .menu > li > a:hover, .main-menu .menu > li > a:focus {
    background: transparent;
    color: #fff; }
  .main-menu .menu > li > a:after {
    content: "\f0d8";
    font-family: FontAwesome;
    color: #F2F2F2;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -18px;
    font-size: 1.2em;
    text-align: center;
    opacity: 0;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out; }
  .main-menu .menu > li > a:hover:after {
    bottom: -7px;
    opacity: 1; }

.main-menu .menu > li.current-menu-item > a {
  color: #fff; }
  .main-menu .menu > li.current-menu-item > a:after {
    bottom: -7px;
    opacity: 1; }

.main-menu .menu .sub-menu {
  position: absolute;
  margin: 0;
  padding: 0;
  list-style: none outside none;
  top: 100%;
  left: 0;
  z-index: 1000;
  background-color: #fff;
  -webkit-box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3); }

.main-menu .menu .sub-menu li {
  float: none; }

.main-menu .menu li > .sub-menu {
  visibility: hidden; }

.main-menu .menu .sub-menu li a {
  white-space: nowrap;
  min-width: 160px; }

.main-menu .menu > li.menu-item-has-children:hover > .sub-menu {
  visibility: visible; }

.main-menu .menu .sub-menu ul {
  left: 100%;
  top: 0; }

.main-menu .menu > li li.menu-item-has-children:hover > .sub-menu {
  visibility: visible; }

.main-menu .menu .sub-menu li a:hover,
.main-menu .menu .sub-menu li.current-menu-item > a,
.main-menu .menu .sub-menu li.current-menu-parent > a {
  color: #fff;
  background: #0b122e;
  -webkit-transition: 0.2s ease-in;
  -moz-transition: 0.2s ease-in;
  -o-transition: 0.2s ease-in;
  transition: 0.2s ease-in; }

.main-menu .menu li > .sub-menu {
  transition: opacity .5s, margin-top .5s, visibility .5s;
  -webkit-transition: opacity .5s, margin-top .5s, visibility .5s;
  -moz-transition: opacity .5s, margin-top .5s, visibility .5s;
  -o-transition: opacity .5s, margin-top .5s, visibility .5s;
  visibility: hidden;
  opacity: 0;
  margin-top: 30px; }

.main-menu .menu li:hover > .sub-menu,
.main-menu .menu li.menu-item-has-children:hover > .sub-menu {
  visibility: visible;
  opacity: 1;
  margin-top: 0; }

