/*--------------------------------------------------------------
# To top
--------------------------------------------------------------*/
.to-top {
  color: #fff;
  z-index: 1040;
  position: fixed;
  bottom: -100px;
  right: 40px;
  font-size: 0.9em;
  opacity: 0;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  padding: 0;
  border: 0;
  box-shadow: none;
  background: #0b122e;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out; }
  @media only screen and (max-width: 480px) {
    .to-top {
      height: 35px;
      width: 35px;
      right: 25px; } }
  .to-top:hover {
    background: #222;
    transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    color: #fff; }
  .to-top.Active {
    opacity: 1;
    bottom: 50px; }
    @media only screen and (max-width: 480px) {
      .to-top.Active {
        bottom: 25px; } }
